.header {
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 120px;
  }
  &__btn{
     background-color: transparent;
     border: none;
     padding: 0;

  }
}

.logo {
  margin: 0 auto;
}

.menu {
  padding-right: 15px;
  &__list {
    display: flex;
  }
  &__list-item{
    padding-right: 45px;
    padding-left: 35px;
  }
}
